import React, { useState, useEffect } from "react";
import {
  Grid,
  Box,
  Container,
  Typography,
  makeStyles,
  Button,
  Link,
  Dialog,
  IconButton,
  DialogContent,
} from "@material-ui/core";
import twitter from "../../assets/icons/TW_icon.svg";
import instagram from "../../assets/icons/IN_icon.svg";
import youtube from "../../assets/icons/YT_icon.svg";
import medium from "../../assets/icons/ME_icon.svg";
import telegram from "../../assets/icons/TG_icon.svg";
import discord from "../../assets/icons/DI_icon.svg";
import errorIcon from "../../assets/icons/error-icon.png";
import { IoIosClose } from "react-icons/io";
import Logo from "src/assets/images/Peculiar-Pugs-Logo-300x300.png";
import Omnihorse from "src/assets/images/Peculiar-Pugs-Logo-300x300.png";
import { preventDefault } from "@fullcalendar/react";
import TermsConditions from "../../views/pages/StaticContent/TermsConditions";
import PrivacyPolicy from "../../views/pages/StaticContent/PrivacyPolicy";
const useStylesNew = makeStyles((theme) => ({
  footerWrap: {
    position: "relative",
    background: "linear-gradient(270deg, #331A41 0%, #20174C 100%)",
    zIndex: "9",
    padding: "0 9.8%",
    overflow: "hidden",
  },
  topWrapContainer: {
    display: "flex",
    flexDirection: "column",
  },
  topWrapContent: {
    display: "flex",
    flexDirection: "column",
    "@media(min-width:1075px)": {
      display: "grid",
      gridTemplateColumns: "1fr 1fr 1fr",
      //   borderBottom: '1px solid rgba(36, 36, 36, 0.2)',
    },
  },
  topLinksContainer: {
    display: "none",
    "@media(max-width:800px)": {
      display: "flex",
      flexDirection: "column",
    },
  },
  topLinksContent: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    marginBottom: "15px",
    "& a": {
      [theme.breakpoints.only("xs")]: {
        fontSize: "13px",
      },
    },
  },
  flx: {
    display: "flex",
    flexDirection: "column",
  },

  // LOGO SECTION
  logoSection: {
    // borderBottom: '1px solid #594868',
    // marginBottom: '27px',
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    // alignItems: 'center',
    // "@media(min-width:1335px)": {
    //   borderBottom: "none",
    //   alignItems: 'start',
    // },
  },
  fullLogo: {
    display: "flex",
    flexDirection: "row",
    alignItem: "center",
    marginTop: "34px",
  },
  logo: {
    height: "50px",
  },
  description: {
    fontSize: "0.9375rem",
    lineHeight: "1.5rem",
    color: "rgba(36, 36, 36, 0.85)",
    textAlign: "center",
    marginBottom: "15px",
    "@media(min-width:1075px)": {
      fontSize: ".75rem",
      lineHeight: "1.125rem",
      maxWidth: "387px",
      textAlign: "left",
    },
  },

  // LINK SECTION
  linkSection: {
    "@media(min-width:1075px)": {
      gridColumn: "3/4",
      gridRow: "1/2",
    },
  },
  linkSectionTop: {
    borderBottom: "1px solid rgba(36, 36, 36, 0.2)",
    paddingBottom: "18px",
    marginBottom: "17px",
    display: "flex",
    justifyContent: "space-around",
    "@media(min-width:1075px)": {
      //   borderBottom: "none",
      marginTop: "39px",
      justifyContent: "center",
      gap: "38px",
      alignItems: "end",
      marginBottom: "auto",
    },
  },
  linkItems: {
    marginTop: "auto",
    display: "flex",
    "& h2": {
      display: "flex",
      color: "#000",
      fontSize: "1.125rem",
      fontWeight: "600",
      lineHeight: "1.5rem",
      marginBottom: "20px",
    },
  },
  listItem: {
    display: "flex",
    marginBottom: "10px",
    fontSize: "1rem",
    lineHeight: "1.5rem",
    color: "rgba(36, 36, 36, 0.85)",
  },

  // STAY IN THE LOOP
  loopSection: {
    // borderBottom: '1px solid #FFFFFF',
    display: "flex",
    flexDirection: "column",
    alignItems: "left",
    marginTop: "0px",
    marginLeft: "0px",
    "@media(min-width:1075px)": {
      //   borderBottom: "none",
      marginTop: "32px",
      marginBottom: "auto",
      gridColumn: "2/3",
      marginLeft: "80px",
    },
    // '& h1': {
    //   color: '#FFFFFF',
    //   fontSize: '1.5rem',
    //   lineHeight: '2rem',
    //   fontWeight: '500',
    //   marginBottom: '20px',
    //   marginTop: '0',
    //   "@media(min-width:1335px)": {
    //     marginBottom: '20px',
    //     textAlign: 'left',
    //     marginRight: '160px',
    //   },
    // },
    "& h6": {
      marginTop: "0",
      color: "#A39AAD",
      fontSize: "14px",
      lineHeight: "1.5rem",
      fontWeight: "500",
      marginBottom: "18px",
      textAlign: "left",

      [theme.breakpoints.only("xs")]: {
        fontSize: "12px",
      },
    },
  },
  loopSectionTitle: {
    fontFamily: "Manrope",
    fontSize: "1.375rem",
    fontWeight: "500",
    textAlign: "left",
    marginBottom: "20px",
    [theme.breakpoints.down("md")]: {
      marginTop: "20px",
      marginBottom: "10px",
    },
  },
  join: {
    color: "#A39AAD",
  },
  subForm: {
    display: "flex",
    maxWidth: "700px",
  },
  subscribe: {
    textAlign: "center",
  },
  subscribeBox: {
    display: "flex",
    justifyContent: "center",
    marginBottom: "11px",
    maxWidth: "300px",
    "@media(min-width:1075px)": {
      justifyContent: "start",
      marginTop: "48px",
      marginBottom: "11px",
      marginRight: "40px",
    },
  },
  errorMessage: {
    fontSize: "12px",
    color: "#fff",
    marginLeft: "5px",
  },
  socials: {
    display: "flex",
    justifyContent: "flex-start",
    marginBottom: "34px",
    columnGap: "15px",
    "@media(min-width:1075px)": {
      display: "none",
    },
    [theme.breakpoints.only("xs")]: {
      marginTop: "24px",
      marginBottom: "24px",
    },
  },
  socialsDesktop: {
    display: "none",
    justifyContent: "flex-start",
    marginBottom: "21px",
    columnGap: "20px",
    marginTop: "10px",
    "@media(min-width:1075px)": {
      display: "flex",
    },
  },
  text: {
    backgroundColor: "#3F3059",
    margin: 0,
    padding: "0 14px",
    width: "250px",
    border: 0,
    borderRadius: "7px",
    color: "#847F96",
    "&::placeholder": {
      color: "#847F96",
    },
    [theme.breakpoints.down("md")]: {
      width: "170px",
    },
  },
  btnSub: {
    color: "white",
    width: "99px",
    border: "none",
    height: "42px",
    fontSize: ".75rem",
    background: "linear-gradient(270deg, #D673DF 0%, #742FD7 100%)",
    fontWeight: "700",
    borderRadius: "5px",
    marginLeft: "8px",
  },

  // LLC SECTION
  llcSectionContainer: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    marginTop: "20px",
    borderTop: "1px solid rgba(255,255,255, 0.3)",
    padding: "50px 0",
    color: "#87819B",
    fontSize: "0.9375rem",
    "@media(max-width:800px)": {
      padding: "25px 0",
    },
  },
  llcSectionLeft: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
  },
  llcSectionRight: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
  },

  llcSectionLeftSplit: {
    display: "none",
    "@media(min-width:800px)": {
      display: "flex",
      flexDirection: "row",
      marginRight: "10px",
      width: "100%",
    },
  },
  llcSectionRightSplit: {
    display: "none",
    "@media(min-width:800px)": {
      display: "flex",
      flexDirection: "row",
      marginLeft: "10px",
      width: "100%",
    },
  },
  leftFooterLinks: {
    marginRight: "20px",
    "& a": {
      [theme.breakpoints.only("xs")]: {
        fontSize: "14px",
      },
    },
  },
  rightFooterLinks: {
    marginLeft: "20px",
    cursor: "pointer",
  },
  omnihorseLinkContainer: {
    "@media(max-width:800px)": {
      display: "flex",
      justifyContent: "center",
      width: "100%",
    },
  },
  omnihorseLink: {
    "@media(max-width:800px)": {
      display: "flex",
      justifyContent: "center",
      width: "100%",
    },
  },

  //   llcSection: {
  //     margin: '26px 36px 0 36px',
  //     paddingBottom: '59px',
  //     "@media(min-width:1335px)": {
  //       borderBottom: "none",
  //       marginTop: '11px',
  //       paddingBottom: '20px',
  //       gridColumn: '2/3',
  //     },

  //   },
  disclaimer: {
    textAlign: "center",
    color: "rgba(124, 124, 124, 0.85)",
    fontSize: ".75rem",
    marginBottom: "21px",
    "@media(min-width:1075px)": {
      marginBottom: "0",
    },
  },
  copyright: {
    textAlign: "center",
    color: "rgba(124, 124, 124, 0.85)",
    fontSize: "1rem",
  },
  dialogBox: {
    display: "flex",
    alignItem: "center",
    padding: "20px",
  },
  responsivelogo: {
    [theme.breakpoints.only("xs")]: {
      width: "125px",
    },
  },
}));

export default function Liquidity() {
  const classesNew = useStylesNew();
  const [loading, setLoading] = useState(false);
  const [privacyAndPolicy, setPrivacyAndPolicy] = useState(false);
  const [termsAndConditions, setTermsAndConditions] = useState(false);
  // props for useStyles

  const termsTermsCloseHandler = () => {
    setTermsAndConditions(false);
  };
  const privacypolicyCloseHandler = () => {
    setPrivacyAndPolicy(false);
  };

  function clickNav(id) {
    if (id === "/") {
      window.scrollTo(0, 0);
    } else {
      const ele = document.getElementById(id);
      if (ele) {
        window.scrollTo(0, ele.offsetTop - 100);
      }
    }
  }

  const [submitted, setSubmitted] = useState(false);
  const [emailAddress, setEmailAddress] = useState("");
  const [errorEmail, setErrorEmail] = useState(false);
  const [errorBackend, setErrorBackend] = useState(false);

  // regex to validate email
  const patternEmail =
    // eslint-disable-next-line no-control-regex
    /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9]))\.){3}(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9])|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/;

  const onInputChange = (event) => {
    const {
      target: { value },
    } = event;
    setErrorBackend(false);
    setErrorEmail(false);
    setEmailAddress(event.target.value);
  };

  const onSubmit = async (event) => {
    event.preventDefault();
    if (!emailAddress) {
      return null;
    } else if (emailAddress && !patternEmail.test(emailAddress)) {
      setErrorEmail(true);
    } else {
      setLoading(true);
      const data = {
        emailAddress: emailAddress,
      };

      const submitForm = await fetch(
        "https://coije1jbmh.execute-api.us-west-1.amazonaws.com/production/subscription",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(data),
        }
      )
        .then((response) => response.json())
        .then((data) => {
          if (data.acknowledged) {
            setLoading(false);
            setSubmitted(data);
          } else if (data.error) {
            setLoading(false);
            setSubmitted(false);
            setErrorBackend(data.error);
          }
        })
        .catch((error) => console.log(error));
    }
  };

  return (
    <Box className={classesNew.footerWrap}>
      <Box className={classesNew.topWrapContainer}>
        <Box className={classesNew.topWrapContent}>
          <Box className={classesNew.logoSection}>
            <Link className={classesNew.fullLogo} href="https://omnihorse.io">
              <img src={Logo} alt="logo" className={classesNew.logo} />
              <img
                src={Omnihorse}
                alt="omnihorse"
                className={classesNew.responsivelogo}
              />
            </Link>
            <Box className={classesNew.socialsDesktop}>
              <Link target="_blank" href="https://discord.gg/kSTRuyy2ec">
                <img src={discord} alt="discord" width="30px" />
              </Link>
              <Link target="_blank" href="https://t.me/omnihorse">
                <img src={telegram} alt="telegram" />
              </Link>
              <Link target="_blank" href="https://twitter.com/omnihorse_NFT">
                <img src={twitter} alt="twitter" />
              </Link>
              <Link
                target="_blank"
                href="https://www.youtube.com/channel/UC_1Jp6Vfbdh_AwNfnU-pa8Q"
              >
                <img src={youtube} alt="youtube" />
              </Link>
              <Link target="_blank" href="https://www.instagram.com/omnihorse/">
                <img src={instagram} alt="instagram" />
              </Link>{" "}
              <Link target="_blank" href="https://medium.com/@OmniHorse_NFT">
                <img src={medium} alt="medium" />
              </Link>
            </Box>
          </Box>
          <Box className={classesNew.loopSection}>
            <Typography className={classesNew.loopSectionTitle}>
              Stay in the loop
            </Typography>
            <Typography className={classesNew.join} variant="h6">
              Join our mailing list to stay in the loop with our newest feature
              releases, NFT drops, and tips and tricks for navigating Omnihorse.
            </Typography>

            <Box className={classesNew.subscribeBox}>
              {submitted && submitted.acknowledged && (
                <div style={{ color: "#fff" }}>
                  Thank you for your submission!
                </div>
              )}
              {!submitted && (
                <form onSubmit={onSubmit} className={classesNew.subForm}>
                  <input
                    className={classesNew.text}
                    placeholder="Enter your email"
                    value={emailAddress}
                    onChange={onInputChange}
                  />
                  <Button
                    type="submit"
                    variant="contained"
                    className={classesNew.btnSub}
                  >
                    Subscribe
                  </Button>
                </form>
              )}
            </Box>
            {errorEmail && (
              <div
                style={{
                  marginTop: "5px",
                  display: "flex",
                  justifyContent: "start",
                  alignItems: "center",
                  flexDirection: "row",
                }}
              >
                <img alt="error icon" src={errorIcon} />
                <div className={classesNew.errorMessage}>
                  Please enter a valid email address
                </div>
              </div>
            )}
            {errorBackend && (
              <div
                style={{
                  marginTop: "5px",
                  display: "flex",
                  justifyContent: "start",
                  alignItems: "center",
                  flexDirection: "row",
                }}
              >
                <img alt="error icon" src={errorIcon} />
                <div className={classesNew.errorMessage}>{errorBackend}</div>
              </div>
            )}
            <Box className={classesNew.socials}>
              <Link target="_blank" href="https://discord.gg/kSTRuyy2ec">
                <img src={discord} alt="discord" width="30px" />
              </Link>
              <Link target="_blank" href="https://twitter.com/omnihorse_NFT">
                <img src={twitter} alt="twitter" />
              </Link>
              <Link target="_blank" href="https://t.me/omnihorse">
                <img src={telegram} alt="telegram" />
              </Link>
              <Link target="_blank" href="https://medium.com/@OmniHorse_NFT">
                <img src={medium} alt="medium" />
              </Link>
              <Link
                target="_blank"
                href="https://www.youtube.com/channel/UC_1Jp6Vfbdh_AwNfnU-pa8Q"
              >
                <img src={youtube} alt="youtube" />
              </Link>
              <Link target="_blank" href="https://www.instagram.com/omnihorse/">
                <img src={instagram} alt="instagram" />
              </Link>{" "}
            </Box>
          </Box>
          {/* <Box className={classesNew.llcMobileContainer}>
                        <Box className={classesNew.llcSectionLeft}>
                            <Box className={classesNew.llcSectionLeftSplitMobile}>
                                <Link className={classesNew.leftFooterLinksMobile} target="_blank" href="https://omnihorse.io/detail">Lady Hollywood</Link>
                                <Link className={classesNew.leftFooterLinksMobile} href="mailto:Contact@omnihorse.com">Email : contact@omnihorse.io</Link>
                            </Box>
                        </Box>
                </Box>
                <Box className={classesNew.llcMobileContainer}>
                        <Box className={classesNew.llcSectionRightSplitMobile}>
                            <Link className={classesNew.rightFooterLinksMobile} href="/terms-of-use">Terms of Services</Link>
                            <Link className={classesNew.rightFooterLinksMobile} href="/privacy">Privacy Policy</Link>
                        </Box>
                </Box> */}
        </Box>

        {/* For mobile screens */}
        <Box className={classesNew.topLinksContainer}>
          <Box className={classesNew.topLinksContent}>
            <Link target="_blank" href="https://omnihorse.io/detail-lh">
              Lady Hollywood
            </Link>
            <Link target="_blank" href="https://omnihorse.io/detail">
              Metahorse
            </Link>
            <Link target="_blank" href="https://omnihorse.io/detail-cg">
              California Gem
            </Link>
            <Link target="_blank" href="https://omnihorse.io/detail-gm">
              Girl Magic
            </Link>
          </Box>

          <Box className={classesNew.topLinksContent}>
            <Link target="_blank" href="https://whitepaper.omnihorse.io/">
              Whitepaper
            </Link>
            <Link onClick={() => setTermsAndConditions(true)}>
              Terms of Services
            </Link>
            <Link onClick={() => setPrivacyAndPolicy(true)}>
              Privacy Policy
            </Link>
            <Link href="mailto:contact@omnihorse.io">Email</Link>
          </Box>
        </Box>
      </Box>

      {/* For Wide Screens */}
      <Box className={classesNew.llcSectionContainer}>
        <Box className={classesNew.llcSectionLeft}>
          <Box className={classesNew.llcSectionLeftSplit}>
            <Link
              className={classesNew.leftFooterLinks}
              target="_blank"
              href="https://omnihorse.io/detail-lh"
            >
              Lady Hollywood
            </Link>
            <Link
              className={classesNew.leftFooterLinks}
              target="_blank"
              href="https://omnihorse.io/detail"
            >
              Metahorse
            </Link>
            <Link
              className={classesNew.leftFooterLinks}
              target="_blank"
              href="https://omnihorse.io/detail-cg"
            >
              California Gem
            </Link>
            <Link
              className={classesNew.leftFooterLinks}
              target="_blank"
              href="https://omnihorse.io/detail-gm"
            >
              Girl Magic
            </Link>
            <Link
              className={classesNew.leftFooterLinks}
              href="mailto:Contact@omnihorse.io"
            >
              contact@omnihorse.io
            </Link>
          </Box>
        </Box>
        <Box className={classesNew.llcSectionRight}>
          {/* <Box className={classesNew.llcSectionRightSplit}>
            <Link target="_blank" href="https://whitepaper.omnihorse.io/">
              Whitepaper
            </Link>
            <Link className={classesNew.rightFooterLinks} onClick={() => setTermsAndConditions(true)}>
              Terms of Use
            </Link>
            <Link className={classesNew.rightFooterLinks} onClick={() => setPrivacyAndPolicy(true)}>
              Privacy Policy
            </Link>
          </Box> */}
        </Box>
        <Box className={classesNew.omnihorseLinkContainer}>
          <Box className={classesNew.llcSectionRightSplit}>
            <Link target="_blank" href="https://whitepaper.omnihorse.io/">
              Whitepaper
            </Link>
            <Link
              className={classesNew.rightFooterLinks}
              onClick={() => setTermsAndConditions(true)}
            >
              Terms of Use
            </Link>
            <Link
              className={classesNew.rightFooterLinks}
              onClick={() => setPrivacyAndPolicy(true)}
            >
              Privacy Policy
            </Link>
            <Link
              className={classesNew.rightFooterLinks}
              target="_blank"
              href="https://omnihorse.io"
            >
              &copy; 2022 Omnihorse
            </Link>
          </Box>
        </Box>
      </Box>

      <Dialog
        onClose={() => setTermsAndConditions(false)}
        aria-labelledby="customized-dialog-title"
        open={termsAndConditions}
        maxWidth="sm"
        fullWidth
      >
        <Box
          display="flex"
          alignItems="center"
          pb={2}
          style={{ borderBottom: "1px solid rgb(234 236 243 / 37%)" }}
          classesNew={classesNew.dialogBox}
        >
          <Typography
            variant="h2"
            style={{
              color: "#fff",
              fontSize: "25px",
            }}
          >
            Terms Of Use
          </Typography>
          <IconButton
            onClick={() => setTermsAndConditions(false)}
            style={{
              color: "#fff",
              right: "0px",
              position: "absolute",
            }}
          >
            <IoIosClose />
          </IconButton>
        </Box>

        <Box mt={2} mb={2}></Box>

        <DialogContent>
          <Box>
            <TermsConditions termsTermsCloseHandler={termsTermsCloseHandler} />
          </Box>
        </DialogContent>
      </Dialog>

      <Dialog
        onClose={() => setPrivacyAndPolicy(false)}
        aria-labelledby="customized-dialog-title"
        open={privacyAndPolicy}
        maxWidth="sm"
        fullWidth
      >
        <Box
          display="flex"
          alignItems="center"
          pb={2}
          style={{ borderBottom: "1px solid rgb(234 236 243 / 37%)" }}
          classesNew={classesNew.dialogBox}
        >
          <Typography
            variant="h2"
            style={{
              color: "#fff",
              fontSize: "25px",
            }}
          >
            Privacy Policy
          </Typography>
          <IconButton
            onClick={() => setPrivacyAndPolicy(false)}
            style={{
              color: "#fff",
              right: "0px",
              position: "absolute",
            }}
          >
            <IoIosClose />
          </IconButton>
        </Box>

        <Box mt={2} mb={2}></Box>

        <DialogContent>
          <Box>
            <PrivacyPolicy
              privacypolicyCloseHandler={privacypolicyCloseHandler}
            />
          </Box>
        </DialogContent>
      </Dialog>
    </Box>
  );
}
