import React from "react";
import { makeStyles, Box, Typography, Container,Button } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    // paddingTop: "84px",
    // paddingBottom: "40px",
    "& .headbox": {
      // backgroundImage: `url(images/terms.png)`,
      display: "flex",
      alignItems: "center",

      "& h1": {
        color: "white",
        fontSize: "18px",
        lineHeight: "46px",
      },
    },
    "& .mainbox": {
      // padding: "30px",

      borderRadius: "10px",
      "& p": {
        color: "white",
        "@media(max-width:767px)": {
          fontSize: "14px",
        },
      },
      "& .text": {
        "& h4": {
          textAlign: "inherit",
          color: "white",
          fontWeight: "600",
          padding: "7px 0px",
          lineHeight: "22px",
        },
        '& h5': {
          marginTop: '20px',
          textDecoration: 'underline',
        },
        "& h6": {
          textAlign: "inherit",
          color: "#EAE9E9",
          fontWeight: "400",
          padding: "7px 0px",
          lineHeight: "22px",
          fontSize: "14px",
        },
      },
    },
  },
  lnk: {
    marginLeft: '2px',
    color: '#ad3bf9',
  }
}));

function Terms(props) {
  const classes = useStyles();
  const {privacypolicyCloseHandler} = props;
  return (
      <Box className={classes.root}>
        <Box className="headbox">
          <Typography variant="h1">Privacy Policy</Typography>
        </Box>

          <Box className="mainbox">
            <Box className="text">
              <Typography variant="h5" style={{color:"#fff"}}> <b>Privacy Policy for Omnihorse</b> </Typography>
                <Typography variant="h6">
                  At Omnihorse one of our main priorities is the privacy of our visitors. This Privacy Policy document contains types of information that is collected and recorded by Omnihorse and how we use it. 
                  <br/>If you have additional questions or require more information about our Privacy Policy, do not hesitate to contact us. 
                  <br/>This privacy policy applies only to our online activities and is valid for visitors to our website with regards to the information that they shared and/or collected in omnihorse.io. This policy is not applicable to any information collected offline or via channels other than this website. By using our website, you hereby consent to our Privacy Policy and agree to its terms.
                </Typography>
                <Typography variant="h5" style={{color:"#fff"}}> <b>Information we collect</b></Typography>
                <Typography variant="h6">
                  The personal information that you are asked to provide, and the reasons why you are asked to provide it, will be made clear to you at the point we ask you to provide your personal information. 
                  <br/><br/>If you contact us directly, we may receive additional information about you such as your name, email address, phone number, the contents of the message and/or attachments you may send us, and any other information you may choose to provide. We may ask for your contact information, including items such as name, company name, address, email address, and telephone number. 
                  
                  <br/><br/>How we use your information <br/>
                  We use the information we collect in various ways, including to:<br/><br/>
                  <ul>
                    <li>Provide, operate, and maintain our website</li> 
                    <li>Improve, personalize, and expand our website</li> 
                    <li>Understand and analyze how you use our website</li> 
                    <li>Develop new products, services, features, and functionality</li> 
                    <li>Communicate with you, either directly or through one of our partners, including for customer service, to provide you with updates and other information relating to the website, and for marketing and promotional purposes</li> 
                    <li>Send you emails</li> 
                    <li>Find and prevent fraud</li> 
                    <li>Log Files</li> 
                  </ul>
                  <br/><br/>Omnihorse follows a standard procedure of using log files. These files log visitors when they visit websites. All hosting companies do this and a part of hosting services' analytics. The information collected by log files include internet protocol (IP) addresses, browser type, Internet Service Provider (ISP), date and time stamp, referring/exit pages, and possibly the number of clicks. These are not linked to any information that is personally identifiable. The purpose of the information is for analyzing trends, administering the site, tracking users' movement on the website, and gathering demographic information. 
                  
                </Typography>
                <Typography variant="h5" style={{color:"#fff"}}> <b>Cookies and Web Beacons</b> </Typography>
                <Typography variant="h6">
                  Like any other website, Omnihorse uses cookies'. These cookies are used to store information including visitors' preferences, and the pages on the website that the visitor accessed or visited. The information is used to optimize the users' experience by customizing our web page content based on visitors' browser type and/or other information. 
                  <br/><br/>You can choose to disable cookies through your individual browser options. To know more detailed information about cookie management with specific web browsers, it can be found at the browsers' respective websites.
                </Typography>
                <Typography variant="h5" style={{color:"#fff"}}> <b>CCPA Privacy Policy (Do Not Sell My Personal Information)</b></Typography>
                <Typography variant="h6">
                  Under the CCPA, among other rights, California consumers have the right to: 
                  <br/><br/>Request that a business that collects a consumer's personal data disclose the categories and specific pieces of personal data that a business has collected about consumers. 
                  <br/>Request that a business delete any personal data about the consumer that a business has collected. 
                  <br/>Request that a business that sells a consumer's personal data, not sell the consumer's personal data. 
                  <br/>If you make a request, we have one month to respond to you. If you would like to exercise any of these rights, please contact us. 
                </Typography>
                <Typography variant="h5" style={{color:"#fff"}}> <b>GDPR Privacy Policy (Data Protection Rights)</b></Typography>
                <Typography variant="h6">
                  <br/>We would like to make sure you are fully aware of all your data protection rights. Every user is entitled to the following: 
                  <br/>The right to access  You have the right to request copies of your personal data. We may charge you a small fee for this service. 
                  <br/>The right to rectification  You have the right to request that we correct any information you believe is inaccurate. You also have the right to request that we complete the information you believe is incomplete. 
                  <br/>The right to erasure  You have the right to request that we erase your personal data, under certain conditions. 
                  <br/>The right to restrict processing  You have the right to request that we restrict the processing of your personal data, under certain conditions. 
                  <br/>The right to object to processing  You have the right to object to our processing of your personal data, under certain conditions. 
                  <br/>The right to data portability  You have the right to request that we transfer the data that we have collected to another organization, or directly to you, under certain conditions. 
                  <br/>If you make a request, we have one month to respond to you. If you would like to exercise any of these rights, please contact us. 
                </Typography>
                  
                <Typography variant="h5" style={{color:"#fff"}}> <b>Children's Information </b></Typography>
                <Typography variant="h6">
                  Another part of our priority is adding protection for children while using the internet. We encourage parents and guardians to observe, participate in, and/or monitor and guide their online activity. 
                  <br/>Omnihorse does not knowingly collect any personal identifiable Information from children under the age of 13. If you think that your child provided this kind of information on our website, we strongly encourage you to contact us immediately and we will do our best efforts to promptly remove such information from our records. 
                </Typography>
                <Typography variant="h5" style={{color:"#fff"}}> <b>Questions</b></Typography>
                <Typography variant="h6">
                  If you have any questions about this Privacy Policy, please
                  <span>
                    <a className={classes.lnk} href='mailto:Contact@omnihorse.com'>
                       "contact us."
                    </a>
                  </span>
                </Typography>
              </Box>
            </Box>
            <Box align="center" my={4}>
            <Button variant="contained" color="primary" size="large" onClick={privacypolicyCloseHandler}
            style={{background: "linear-gradient(270deg, #D673DF 0%, #742FD7 100%)", minWidth:"250px"}}>I got it!</Button>
          </Box>
      </Box>
  );
}

export default Terms;
