import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter } from "react-router-dom";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import "../src/scss/main.css";
import "../src/scss/main.scss";
import { Web3Provider } from "@ethersproject/providers";
import { createWeb3ReactRoot, Web3ReactProvider } from "@web3-react/core";
import { NetworkContextName } from "src/constants";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { MoralisProvider } from "react-moralis";
const Web3ProviderNetwork = createWeb3ReactRoot(NetworkContextName);
 
function getLibrary(provider) {
  const library = new Web3Provider(provider);
  library.pollingInterval = 15000;
  return library;
}

ReactDOM.render(
  <BrowserRouter>
    <Web3ReactProvider getLibrary={getLibrary}>
      <Web3ProviderNetwork getLibrary={getLibrary}>
        {/*testnet*/}
        {/* <MoralisProvider appId={`${process.env.REACT_APP_MORALIS_ID}`} serverUrl={`${process.env.REACT_APP_MORALIS_URL}`}> */}
        <MoralisProvider appId={"BLTpWauTTIcPXO9d9PPLOYMoWg4E6sTAwedDl1fz"} serverUrl={"https://0wncjwpacla5.usemoralis.com:2053/server"}>
        {/*mainnet*/}
        {/* <MoralisProvider appId={"FiWDU5CRIFi3X0K56zRCxzEwIwfNAT7nxPIjjBt5"} serverUrl={"https://m11mspesez3p.usemoralis.com:2053/server"}> */}
          <App />
        </MoralisProvider>
        <ToastContainer />
      </Web3ProviderNetwork> 
    </Web3ReactProvider> 
  </BrowserRouter>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
