import { Box, Button, makeStyles } from "@material-ui/core";
import React from "react";
import pic from '../../../../assets/svg/landingPage/btnConnect.svg';
import BtnFlipConnect from '../../../../assets/svg/landingPage/BtnFlipConnect.svg'
import  btnLargeOn from '../../../../assets/svg/buttons/btnLargeOn.svg'
import  btnLargeOff from '../../../../assets/svg/buttons/btnLargeOff.svg'


const BtnConnect = (props) => {
  
  const useStyles = makeStyles((theme) => ({
    buttonSvg: {
      backgroundImage: "linear-Gradient(to left, #D472DF 10%, #8038D6 100%)",
      backgroundColor: 'transparent',
      backgroundSize: '100% 100%',
      border: 0,
      borderRadius: "15px",
      padding: '0px',
      color: '#FFF',
      lineHeight: '1.5rem',
      fontSize: '1rem',
      fontWeight: '700',
      fontFamily: 'Manrope',
      minWidth: '197px',
      maxWidth: '197px',
      height: "48px",
      cursor: 'pointer',
      // '&:hover': {
      //   backgroundImage: `url(${btnLargeOn})`,
      // }
    },
    buttonSvgDc: {
      backgroundImage: "linear-Gradient(to left, #D472DF 10%, #8038D6 100%)",
      backgroundColor: 'transparent',
      backgroundSize: '100% 100%',
      borderRadius: "15px",
      lineHeight: '1.5rem',
      border: 0,
      padding: '10px',
      color: '#FFF',
      textTransform: 'uppercase',
      fontSize: '1rem',
      fontWeight: '600',
      fontFamily: 'Manrope',
      minWidth: '197px',
      maxWidth: '197px',
      height: "48px",
      overflow: 'hidden',
      whiteSpace: 'nowrap',
      textOverflow: 'ellipsis',
      cursor: 'pointer',
      // '&:hover': {
      //   backgroundImage: `url(${btnLargeOn})`,
      // }
    },
    
  }));

  const classes = useStyles();
  
  return (
    <>
      { props.account ? (
        <button className={classes.buttonSvgDc} onClick={props.onClick}>
          Disconnect {props.account}
        </button>
      ) : (
        <button className={classes.buttonSvg} onClick={props.onClick}>
          Connect Wallet
        </button>
      )}
    </>
  )
}

export default BtnConnect
