const styles = {
  bgColor: "transparent",
  titleTextColor: "white",
  // titleTextSize: "60px",
  rowTitleColor: "white",
  rowContentColor: "white",
  rowContentPaddingTop: "20px",
  rowContentPaddingBottom: "30px",
  arrowColor: "white"
}

const config = {
  animate: true,
  openOnLoad: 0,
  expandIcon: "+",
  collapseIcon: "x"
}

const useStyles = makeStyles((theme) => ({
  contentWrapper: {
    display: 'flex',
    justifyContent: 'center',
    margin: '0 10%',
    marginBottom: '88px',
    // '@media(max-width:1345px)': {
    //   margin: '0 26px',
    // },
    '@media(max-width:900px)': {
      margin: '0 24px 50px 24px',
    }
  },
  FAQ: {
    maxWidth: '1280px',
    marginBottom: "100px",
    // background:"#E5,47FF",
    "@media(max-width: 1200px)": {
      gridTemplateColumns: '1fr',
    },
    [theme.breakpoints.down("md")]: {
      marginBottom: "80px",
    },
    
    "& h3": {
      fontFamily: "Manrope",
      fontWeight: "400",
      fontSize: "35px",
      lineHeight: "40px",
      color: "#FFFEFD",
      marginBottom: "40px",
      textShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
      "@media(max-width: 1200px)": {
        marginBottom: '20px'
      },
      "@media(max-width: 900px)": {
        fontSize: '30px',
        lineHeight: '35px',
        marginBottom: '20px'
      },
    },
  },
}));

export default function FAQ() {
  const classes = useStyles();

  return (
    <Box className={classes.contentWrapper} id="faq">
      <Box>
        <Grid>
          <Faq className="faq-row-wrapper" data={data} styles={styles} config={config} />
        </Grid>
      </Box>
    </Box>
  );
}
